@font-face {
    font-family: 'KimjungchulMyungjo-Bold';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2302_01@1.0/KimjungchulMyungjo-Bold.woff2') format('woff2');
    font-weight: 700;
    font-style: normal;
}

.container {
    font-family: 'Arial', sans-serif;
    text-align: center;
}

.card {
    border: 5px solid #000;
    background-color: white;
    border-radius: 10px;
    padding: 20px;
    margin: 10px;
    margin-top: 210px;
    display: inline-block;
    vertical-align: top;
    width: 800px;
    height: 600px;
}

.icon-container {
    padding: 20px;
    display: flex;
    justify-content: center;
}

.icon {
    margin: 50px;
    width: 200px;
    height: auto;
}

.btn {
    border: none;
    border-radius: 20px;
    padding: 10px 20px;
    color: #000;
    font-weight: bold;
    cursor: pointer;
    margin-top: 40px;
    background-color: #e9e9e9;
}

.loginbtn{
    position: fixed;
    top: 100px;
    right: 320px;
    border-radius: 20px;
    padding: 10px 20px;
    border: 3px solid #000;
    border-radius: 20px;
    color: #000;
    font-weight: bold;
    cursor: pointer;
    background-color: #ffffff;
}

.signbtn{
    position: fixed;
    top: 100px;
    right: 220px;
    border-radius: 20px;
    padding: 10px 20px;
    border: 3px solid #000;
    border-radius: 20px;
    color: #000;
    font-weight: bold;
    cursor: pointer;
    background-color: #ffffff;
}


p {
    margin: 5px 0;
}

.header {
    display: flex; 
    justify-content: space-between; 
    width: 100%;
    align-items: center;
    flex-wrap: nowrap;
    background-color: antiquewhite;
    border: #000;
}

h1 {
    font-family: 'KimjungchulMyungjo-Bold';
    font-size: 70px; 
    text-align: left;
}

.mainbtn {
    position: fixed;
    padding-bottom: 40%;
    right: 78%;
    background-color: transparent;
    border: none;
    cursor: pointer;
}
